<template>
  <div class="title">
    <p>{{msg}}<span>{{msm}}</span></p>
  </div>
</template>

<script>
  export default {
    props:['msg','msm'],
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
  .title{
    margin-left:1rem;
    padding-top:1rem;
    margin-bottom:1rem;
    box-sizing: border-box;
  }
  .title p{
    border-left:0.33rem solid #e46c0a;
    font-size:0.9rem;
    text-align: left;
    color: #333;
    letter-spacing:0.1rem;
    padding-left:0.5rem;
  }
  .title span{
    font-size:0.8rem;
    text-align: left;
    color: #919599;
    padding-left:0.2rem;
    letter-spacing:0;
  }
</style>



