            pic:require("../assets/images/home/index_project_pic01.png"),
            title:"网络工程培训",
            con:"本专业是电子信息技术与计算机科学技术融合的宽口径专业，培养学生系统掌握网络工程技术与信息安...",
            class:"slideInLeft"
          },
