<template>
  <div id="learnour" class="online">
    <Banner msg="Online education" msm="在线教育"></Banner>
    <Nav :msg="nav" :msm="nav2"></Nav>
    <div class="learnour-con">
      <div class="learnour-center">
        <ul class="left-list animated slideInLeft">
          <li v-for="( item , i ) in listL">
            <h2  @click="hDian(item)">{{item.title}}<span v-if="item.spanAC1">+</span><span v-if="item.spanAC2">-</span></h2>
            <p v-for="( its , m ) in item.fen" :class="{fontY:isRight(i,m)}" v-if="item.pActive" @click="pActive(i,m,its)">{{its}}</p>
          </li>
        </ul>
        <ul class="right-list animated slideInRight">
          <li v-for="( list , n) in listR">
            <div v-for="( item , c) in list.con" :class="{display:isRight(n,c)}">
              <h4>{{item.title}}</h4>
              <p v-for="itt in item.thing">{{itt}}</p>
            </div>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
  import Banner from "../../components/banner"
  import Nav from "../../components/nav"
  export default {
    components: {
      Banner, Nav,
    },
    data() {
      return {
        nav: "首页 > 在线教育 >自学考试 >",
        nav2: "北京交通大学",
        nowindex:[0,0],
        listL:[
          {
            title:"学校名称",
            spanAC1:false,
            spanAC2:true,
              fen:["北京交通大学","海口经济学院"],
            pActive:true,
          }
        ],
        listR:[
          {
            con:[
              {
                title:"北京交通大学",
                thing:[
                  "北京交通大学（Beijing Jiaotong University）位于中国政治、文化、科教中心首都北京，是教育部直属高校，由教育部、中国铁路总公司、北京市共建的全国重点大学，是北京高科大学联盟成员高校，是国家“211工程”、“985工程优势学科创新平台”、“2011计划”、“111计划”、“卓越工程师教育培养计划”项目建设高校。学校是国家建设高水平大学公派研究生项目签约高校，是具有研究生院的首批博士、硕士学位授予权高校。",
                  "北京交通大学招生专业：视觉传达设计、房屋建筑工程、财务管理、工程管理。",
                  "学制：1.5年。"
                ]
              },
              {
                title:"海口经济学院",
                thing:[
                  "海口经济学院是一所以经济、管理类专业为主，文、理、工、艺术等并存的多科性全日制普通本科院校。学校创办于1974年，2008年3月经教育部批准升格为本科院校。",
                  "截至2012年9月6日，学校占地1800亩，建筑面积60多万平方米。教学仪器设备总值约9000万元。图书馆大楼建筑面积达4万平米、阅览座位4000余个、馆藏图书114.5万册；拥有可容纳2800多人的大剧场和现代化的音乐厅与演播厅；体育设施齐全：有两个大型田径运动场，一个综合体育训练馆，一个室内游泳馆及多个灯光篮球场、排球场与网球场。",
                  "学院下设12个二级院系和4个教学系部，设置专业近50个，涉及经济、管理、工学、文学、法学、艺术、教育等学科门类，面向全国30个省、市、自治区招生，有全日制本、专科在校生2.2万人。",
                  "海口经济学院招生专业：视觉传达设计、房屋建筑工程、财务管理、工程管理。",
                  "学制：1.5年。"
                ]
              }
            ]
          }
        ]
      }
    },
    methods:{
      pActive(a,b,con){
        this.nowindex = [a,b];
        this.nav2=con;
      },
      hDian(item){
        item.spanAC1=!item.spanAC1;
        item.spanAC2=!item.spanAC2;
        item.pActive=!item.pActive;
      },
      isRight(a,b){
        if(this.nowindex[0]===a&&this.nowindex[1]===b){
          return true;
        }else {
          return false;
        }
      },
      menu() {
        window.scrollTo(0,0);
      }
    },
    created(){
      this.menu();
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style></style>



