<template>
  <div class="coo-aca">
    <div class="txt">
      <h3>合作院校</h3>
      <p>Cooperate academy</p>
    </div>
    <div class="contain" :class="{change:dian}">
      <dl v-for="item in coo">
        <dt><img :src="item.pic"></dt>
        <dd>{{item.name}}</dd>
      </dl>
    </div>
    <div class="lamore" @click="school" :class="{back:dian}"></div>
  </div>
</template>

<script>
export default {
  data(){
    return{
      dian:false,
      coo:[
        {
          pic:require('../assets/images/aca/huazhong.jpg'),
          name:'华中师范大学'
        },
        {
          pic:require('../assets/images/aca/jilin.jpg'),
          name:'吉林大学'
        },
        {
          pic:require('../assets/images/aca/xinan.jpg'),
          name:'西南交通大学'
        },
        {
          pic:require('../assets/images/aca/beijingw.jpg'),
          name:'北京外国语大学'
        },
        {
          pic:require('../assets/images/aca/beijingy.jpg'),
          name:'北京语言大学'
        },
        {
          pic:require('../assets/images/aca/zdnblg.jpg'),
          name:'浙江大学宁波理工'
        },
        {
          pic:require('../assets/images/aca/zjwgy.jpg'),
          name:'浙江外国语学院'
        },
        {
          pic:require('../assets/images/aca/wuhanf.jpg'),
          name:'武汉纺织大学'
        },
        {
          pic:require('../assets/images/aca/wuhank.jpg'),
          name:'武汉科技大学'
        },
      ]
    }
  },
  methods:{
    school(){
      this.dian=!this.dian;
    }
  }
}
</script>

<!-- Add "scoped" attribu te to limit CSS to this component only -->
<style>
  .coo-aca{
    position: relative;
    width:48rem;
    padding:1.5rem 0;
    background:#fff;
    box-shadow: 0 0 0.1rem 0.01rem #ccc;
    display:flex;
    align-items:center;
  }
  .coo-aca .txt{
    width:8.5rem;
    margin-left:1.2rem;
  }
  .coo-aca .txt h3{
    font-size:0.9rem;
    color: #17375f;
    font-weight:500;
    text-align:left;
    letter-spacing:0.1rem;
  }
  .coo-aca .txt p{
    font-size:0.73em;
    color: #aaaeb3;
    text-align:left;
  }
  .coo-aca .contain{
    width:30.8rem;
    height:3rem;
    overflow: hidden;
  }
  .coo-aca .change{
    height:auto;
    overflow: visible;
  }
  .coo-aca .contain dl{
    float:left;
    width:5rem;
    margin-bottom:1rem;
  }
  .coo-aca .contain dl dt{
    width:1.66rem;
    height:1.66rem;
    margin:0 auto;
  }
  .coo-aca .contain dl dt img{
    width:1.66rem;
    height:1.66rem;
  }
  .coo-aca .contain dl dd{
    font-size:0.5rem;
    color:#b3b3b3;
    margin-top:0.2rem;
    text-align:center;
  }
  .coo-aca .lamore{
    position: absolute;
    right:2rem;
    top:2rem;
    width:1.5rem;
    height:1em;
    background:url("../assets/images/aca/bottom.png") no-repeat;
    background-size:100%;
    cursor:pointer;
  }
  .coo-aca .back{
    background:url("../assets/images/aca/up.png") no-repeat no-repeat;
    background-size:100%;
  }
</style>



