import Vue from 'vue'
import Router from 'vue-router'
import Home from '../pages/home'
import About from '../pages/about'
import Eduintro from '../pages/eduintro'
import News from '../pages/news'
import Admission from '../pages/admission'
import Study from '../pages/study'
import Newxiang from '../pages/newxiang'
import Adult from '../pages/online/adult'
import Netedu from '../pages/online/netedu'
import Learnself from '../pages/online/learnself'
import Onjob from '../pages/online/onjob'
import Cd from '../pages/news/cd'
import Cp from '../pages/news/cp'
import En from '../pages/news/en'
import Id from '../pages/news/id'
import Rp from '../pages/online/net/rp'
import Rc from '../pages/online/net/rc'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      component: Home
    },
    {
      path: '/home',
      component: Home
    },
    {
      path: '/about',
      component: About
    },
    {
      path: '/eduintro',
      component: Eduintro
    },
    {
      path: '/news',
      component: News
    },
    {
      path: '/newxin',
      component: Newxiang
    },
    {
      path: '/admission',
      component: Admission
    },
    {
      path: '/study',
      component: Study
    },
    {
      path: '/adult',
      component: Adult
    },
    {
      path: '/netedu',
      component: Netedu
    },
    {
      path: '/learnself',
      component: Learnself
    },
    {
      path: '/onjob',
      component: Onjob
    },
    {
      path: '/cd',
      component: Cd
    },
    {
      path: '/cp',
      component:Cp
    },
    {
      path: '/en',
      component: En
    },
    {
      path: '/id',
      component: Id
    },
    {
      path: '/rp',
      component: Rp
    },
    {
      path: '/rc',
      component: Rc
    },
  ]
})



