                sdown6:"华中师范大学网络教育宁波大学学习中心的主要工作：",
                band6:[
                  {
                    cta:"（1）组织生源，协助华师网院实施招生和学员入学环节的工作，包括接受学员报名、组织入学考试，发放录取通知书；"
                  },
                  {
                    cta:"（2）为学员的学习提供基本条件；"
                  },
                  {
                    cta:"（3）负责学员的日常管理和协助华师网院搞好学员学籍管理；"
                  },
                  {
                    cta:"（4）负责学员的日常教学辅导以及部分课程的考前辅导；"
                  },
                  {
                    cta:"（5）组织考试，负责考务工作；"
                  },
                  {
                    cta:"（6）代收学员学费。"
                  }
                ],
                endPic:[
                  {
                    url:require("../assets/images/admission/pic6-1-1.jpg")
                  },
                  {
                    url:require("../assets/images/admission/pic6-1-2.jpg")
                  }
                ]
              },
            ]
          }
        ]
      }
    },
    methods:{
      pActive(a,b,con){
        this.nowindex = [a,b];
        this.nav2=con;
      },
      hDian(item){
        item.spanAC1=!item.spanAC1;
        item.spanAC2=!item.spanAC2;
        item.pActive=!item.pActive;
      },
      isRight(a,b){
        if(this.nowindex[0]===a&&this.nowindex[1]===b){
          return true;
        }else {
          return false;
        }
      },
      menu() {
        window.scrollTo(0,0);
      }
    },
    created(){
      this.menu();
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
  #admission{
    width:100%;
  }
</style>



