<template>
  <div id="eduintro">
    <Banner msg="entrance guide" msm="入学指南"></Banner>
    <Nav :msg="nav" :msm="nav2"></Nav>
    <div class="ed-con">
      <div class="ed-center">
        <ul class="left-list animated slideInLeft">
          <li v-for="( item , i ) in listL">
            <h2  @click="hDian(item)">{{item.title}}<span v-if="item.spanAC1">+</span><span v-if="item.spanAC2">-</span></h2>
            <p v-for="( its , m ) in item.fen" :class="{fontY:isRight(i,m)}" v-if="item.pActive" @click="pActive(i,m,its)">{{its}}</p>
          </li>
        </ul>
        <ul class="right-list animated slideInRight">
          <li v-for="( list , n) in listR">
            <div v-for="( item , c) in list.con" :class="{display:isRight(n,c)}">
              <h3>{{item.title}}</h3>
              <p>{{item.top}}</p>
              <h4>{{item.h4}}</h4>
              <p v-for="its in item.con">
                {{its.ding}}
                <span>{{its.ju1}}</span>
                <span>{{its.ju2}}</span>
                <span>{{its.ju3}}</span>
                <span>{{its.ju4}}</span>
                <span>{{its.ju5}}</span>
                <span>{{its.ju6}}</span>
              </p>
            </div>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
  import Fix from "../components/fix"
  import Banner from "../components/banner"
  import Nav from "../components/nav"
  export default {
    components:{
      Banner,Nav,Fix
    },
    data(){
      return {
        nav: "首页 > 入学指南 > ",
        nav2:" 学历分类",
        nowindex:[0,0],
        listL:[
          {
            title:"入学必看",
            spanAC1:false,
            spanAC2:true,
            fen:["学历分类","学历要求","学苑建议"],
            pActive:true,
          },
        ],
        listR:[
          {
            con:[
              {
                title:"学历分类",
                top:"报考学历很重要的一个事情就是先清楚您应该报考哪种性质的学历，其次才是选择学校，选择专业。学历分类很多，很多学生中的观念很有可能是错误的，人人学苑为此开设学历教育入门，希望对您有所帮助。",
                h4:"1.学历分类入门知识",
                show:true,
                con:[
                  {
                    ding:"1.1按层次分类：这是学历的最基本分类，按专科、本科来分，再次不做展开。",
                  },
                  {
                    ding:"1.2按公民分类：在层次分类的基础上，学历分成公办学历、民办学历。",
                    ju1:"公办学历是国家教育系列的学历，民办学历是非国民教育系列的学历；再清楚些就是公办学历是学信网（中国高等教育学生信息网http://www.chsi.com.cn）能查到，而民办学历是中国民办高等教育学生信息网（http://www.crhsi.com.cn/）能查到或者学校官网能查到。"
                  },
                  {
                    ding:"1.3按等级分类：这个是很多学生所不是很懂的分类了，报考学历清楚此分类十分重要。",
                  },
                  {
                    ding:"1.3.1学历等级中的第一个分类（在此只讲公办学历）就是按文凭的“含金量”或者“硬度”来分类。",
                    ju1:"第一：全日制文凭：严格定义应该是统招生的学历，就是必须通过高考考上去的学历；不能报考非高考考上去而去学校全日制读书的学历。",
                    ju2:"第二：自考（又称自学考试）。自考学习形式又分自我学习，全日制助学，业余助学；而最终获取的文凭都是自考办颁发的文凭，文凭无任何的差异。其中的自考业余助学是一个很好的学习形式，有教学灵活，学习轻松，毕业率高等优势。",
                    ju3:"第三：成人教育（又称成人高等教育）：成人教育根据学习形式的不同又分脱产（全日制学习）、业余（俗称夜大，晚上或者周末学习）和函授（自学为主，面授为辅）3种。",
                    ju4:"第四：网络教育（又称远程教育）。网络教育中一个很重要的概念就是学习中心，学习中心负责学生从报名到毕业的所有教学工作，但最终毕业文凭是不体现您在哪个学习中心学习的。",
                    ju5:"第五：电大（又称广播电视大学）。",
                    ju6:"注：以上排名只做参考；网络教育已经是个趋势，某种程度上已经超越了传统的成人教育。"
                  }
                ]
              },
              {
                title:"学历要求",
                top:"报考学历很重要的一个事情就是先清楚您应该报考哪种性质的学历，其次才是选择学校，选择专业。学历分类很多，很多学生中的观念很有可能是错误的，人人学苑为此开设学历教育入门，希望对您有所帮助。",
                h4:"2.学历其他入门知识",
                show:false,
                con:[
                  {
                    ding:"2.1报考学历要求一：报考成人教育、网络教育、电大的本科必须已经有大专学历；而报考自考本科时不需大专学历，自考本科申请毕业有大专学历就行。 所以自考本科可以和别的大学学历一起读，俗称“双学历套读”。此对于是高中学历需要尽快拿本科学历的学生很有用。",
                  },
                  {
                    ding:"2.2报考学历要求二：报考自考大专必须要高中或同等学历，而报考成人教育、网络教育、电大需要高中或同等学历。",
                  },
                  {
                    ding:"2.3报考年龄：报考网络教育大专必须年满18周岁，本科必须年满20周岁；别的无年龄要求。",
                  },
                  {
                    ding:"2.4毕业时间：自考一般需要1.5年到2年；远程教育一般是2.5年，个别专业是3年；成人教育一般是3年；电大一般是3年。",
                  },
                  {
                    ding:"注：提醒考生，因为以上学历都需要先教育部电子注册，学制时间到才能毕业。所以，短于以上时间毕业是不可能的。（自考虽然比较特殊，但短于以上时间的毕业也是不可能的。）"
                  }
                ]
              },
              {
                title:"学院建议",
                top:"报考学历很重要的一个事情就是先清楚您应该报考哪种性质的学历，其次才是选择学校，选择专业。学历分类很多，很多学生中的观念很有可能是错误的，人人学苑为此开设学历教育入门，希望对您有所帮助。",
                h4:"3.人人学苑给予建议",
                show:false,
                con:[
                  {
                    ding:"3.1如果您是高中或同等学历想尽快考本科学历，建议您选择大专和自考本科“套读”，此方法最快。"
                  },
                  {
                    ding:"3.2如果您是全日制大专毕业考本科学历，建议您选择自考本科。别的本科学历的含金量还不如您本身的全日制大学学历。获取自考文凭有很多方法，自考本科通过人人学苑的自考助学，已经不是很难了。",
                  },
                  {
                    ding:"3.3如果您在成人教育和网络教育之间徘徊，建议您选择网络教育。因为网络教育是个趋势，含金量和成人教育已经一样，而且网络教育学制会短。",
                  },
                  {
                    ding:"3.4不要相信不能见面的“学历机构”的学历教育，因为很有可能是假的。学历教育网络上此类机构骗钱很多，请学员慎重。",
                  },
                  {
                    ding:"3.5不要相信短时间能拿到国民教育文凭，上面已经讲明原因。"
                  },
                  {
                    ding:"3.6建议选择能从您报名到您毕业都是您报名机构负责的学历机构报名，不要选择代理机构报名。因为他们的承诺都是他本身不可控的，您会有很大风险。"
                  },
                ]
              }
            ]
          }
        ]
      }
    },
    methods:{
      pActive(a,b,con){
        this.nowindex = [a,b];
        this.nav2=con;
      },
      hDian(item){
        item.spanAC1=!item.spanAC1;
        item.spanAC2=!item.spanAC2;
        item.pActive=!item.pActive;
      },
      isRight(a,b){
        if(this.nowindex[0]===a&&this.nowindex[1]===b){
         return true;
        }else {
          return false;
        }
      },
      menu() {
        window.scrollTo(0,0);
      }
    },
    created(){
      this.menu();
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
  #eduintro{
    width:100%;
  }
</style>



