<template>
  <div id="news">
    <Banner msg="news and trends" msm="新闻动态"></Banner>
    <Nav :msg="nav" :msm="nav2"></Nav>
    <div class="newscon">
      <Cd></Cd>
      <Id></Id>
      <En></En>
      <Cp></Cp>
    </div>
  </div>
</template>

<script>
  import Banner from "../components/banner"
  import Nav from "../components/nav"
  import Cd from "../components/newCd"
  import Id from "../components/newId"
  import En from "../components/newEn"
  import Cp from "../components/newCp"
  export default {
    components: {
      Banner, Nav,Cd,Id,En,Cp
    },
    data(){
      return{
        nav:"首页 > 新闻动态",
        nav2:"",
      }
    },
    methods:{
      menu() {
        window.scrollTo(0,0);
      }
    },
    created(){
      this.menu();
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
  #news{
    width:100%;
  }
</style>



