<template>
  <div class="header">
    <div class="headercon">
      <img src="../assets/images/logo.png" @click="toguan"/>
      <ul class="header-menu">
        <li v-for="(con , i) in allcon" :class="{active: who === i}" @click="change(con.url)">
          {{con.name}}
          <ul class="inner-ul">
            <li v-for="(m , n) in con.erji"  @click.stop="change(m.url);qie(i,n);" >{{m.name}}</li>
          </ul>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        allcon: [
          {
            url: '/home',
            name: '首页',
          },
          {
            url: '/eduintro',
            name: '入学指南',
          },
          {
            name: '在线教育 ▼',
            erji:[
              {
                url: '/adult',
                name: '成人高考',
              },
              {
                url: '/netedu',
                name: '网络教育',
              },
              {
                url: '/learnself',
                name: '自学考试',
              },
              {
                url: '/onjob',
                name: '在职研究生',
              }
            ]
          },
          {
            url: '/news',
            name: '新闻中心 ▼',
            erji:[
              {
                url: '/cd',
                name: '学苑动态',
              },
              {
                url: '/id',
                name: '行业动态',
              },
              {
                url: '/en',
                name: '教务通知',
              },
              {
                url: '/cp',
                name: '常见问题',
              }
            ]
          },
          {
            url: '/admission',
            name: '招生简章',
          },
          {
            url: '/about',
            name: '关于人人',
          },
          {
            name: '旗下网站 ▼',
            erji:[
              {
                name: '职教部',
              },
              {
                name: '学历部',
              },
              {
                name: '留学部',
              },
            ]
          },
        ],
      }
    },
    methods:{
      toguan(){
        this.$router.push({path:'/home'})
      },
      change(url){
        this.$router.push({path:url})
      },
      qie(a,b){
        if( a === 6 ){
          if( b === 0){
            window.location.href="http://zhijiao.zjrrxy.com";
          }
          else if( b === 1 ){
            window.location.href="http://xueli.zjrrxy.com";
          }
          else if( b === 2 ){
            window.location.href="http://guoji.zjrrxy.com";
          }
        }
      }
    },
    computed: {
      who() {
        let a = this.allcon.map((item) => {
          let b;
          if (item.erji) {
            b = item.erji.map((_item) => {
              return this.$route.path === _item.url
            }).indexOf(true);
          }
          if (b>=0) {
            return true
          };
          return this.$route.path === item.url;
        }).indexOf(true);
        return a > 0 ? a : 0;
      },
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
  .header{
    position: fixed;
    left:0;
    top:0;
    width:100%;
    height:2.8rem;
    background:rgba(23,55,95,0.85);
    z-index:999;
  }
  .headercon{
    width:48rem;
    margin:0 auto;
    display:flex;
    align-items:center;
  }
  .headercon img{
    margin-left:1.26rem;
    width:5.6rem;
    cursor:pointer;
  }
  .header-menu{
    margin-left:1rem;
  }
  .header-menu .active{
    background: #fff;
    color:#10466a;
  }
  .header-menu li{
    float: left;
  }
  .header-menu li:hover{
    background: #fff;
    color:#10466a;
  }
  .header-menu li:hover .inner-ul{
    display:block;
  }
  .header-menu li {
    display:block;
    padding:0 1rem;
    font-size:0.8rem;
    height:2.8rem;
    color:#fff;
    line-height:2.8rem;
    cursor:pointer;
  }
  .header-menu li .inner-ul{
    display:none;
    width:0;
    margin-left:-1rem;
  }
  .header-menu li .inner-ul li{
    display:block;
    width:7.13rem;
    height:2rem;
    font-size:0.85rem;
    color:#10466a;
    background:#fff;
    line-height:2rem;
    text-align:left;
    box-sizing:border-box;
    padding-left:1.2rem;
  }
  .header-menu li .inner-ul li:hover{
    background:#17375f;
    color:#fff;
  }
</style>



