<template>
  <div id="about">
    <Banner msg="ABOUT RENREN" msm="关于人人"></Banner>
    <div class="about-one">
      <div class="one-center">
        <h3 class="animated slideInLeft">我们的理念</h3>
        <div class="xin animated slideInLeft" >
          <p>“心系天下求学人”</p>
          <p>Care for all who are eager to learn</p>
        </div>
        <img class="animated slideInRight" src="../assets/images/about/about_part01_pic.png">
      </div>
    </div>
    <div class="about-con">
      <div class="about-pro">
        <div class="pro-one">
          <Title msg="学苑简介/" msm="School profile"></Title>
          <p class="p-name wow slideInLeft">人人学苑是由浙江人人学苑教育科技有限公司组建的教育名牌，总部位于长三角南翼经济中心和浙江省经济中心——宁波，人人学苑最初成立于1999年，秉承“人人有才、人无全才、人尽其才、人人成才”的远景心系天下求学人，为莘莘学子服务。</p>
          <p class="p-name wow slideInLeft">人人学苑参与并举办了华中师范大学网络教育宁波大学学习中心、华中科技大学网络教育宁波大学学习中心、吉林大学网络教育宁波弘成教学服务中心、宁波海曙新世纪教育学校自考学院、武汉科技大学公共管理硕士（MPA双证）浙江学习中心、武汉科技大学工商管理硕士（MBA双证）浙江学习中心、武汉大学软件工程硕士（SE）宁波学习中心等多家教育实体机构，还依托了国内著名高校成立了职业与远程教育研究所。</p>
          <p class="p-name wow slideInRight">人人学苑业务涵盖职业培训、自学考试、成人教育（夜大、函授）、网络教育（远程教育）、在职研究生等；以及驾驶培训的相关业务。</p>
          <p class="p-name wow slideInRight">人人学苑先后与20多所高校及科研机构建立了联系。在宁波萌恒集团、迪蒙电动车、临海海商职业技术学校、三门职业教育集团等单位建立了实习实训和就业基地，构建了职业教育、网络高等教育、成人教育以及在职研究生教育等专业化培养网络。同时，学苑拟成立致力于改善中国贫困山区孩子的教育环境的人人学苑慈善助学基金会得到了各级组织的充分肯定。</p>
        </div>
        <div class="pro-two">
          <dl class="wow" v-for="item in pro2" :class="item.class">
            <dt><img :src="item.pic"></dt>
            <dd>
              <h4>{{item.h4}}</h4>
              <p>{{item.p}}</p>
            </dd>
          </dl>
          <div class="about-line wow slideInRight"></div>
        </div>
      </div>
      <div class="about-hon">
        <div class="hon-one">
          <Title msg="荣誉资质/" msm="Honorary certificate"></Title>
          <div class="hon-ti wow slideInLeft">
            <p>办学许可证</p>
            <p>税务登记证</p>
            <p>企业登记证</p>
            <p>工商联信用认证</p>
          </div>
          <img class="wow slideInRight" src="../assets/images/about/abou_part03_pic01.png">
        </div>
      </div>
      <Coo></Coo>
    </div>
  </div>
</template>

<script>
  import Fix from "../components/fix"
  import Banner from "../components/banner"
  import Title from "../components/title"
  import Coo from "../components/Coo"
  import {WOW} from '../assets/js/wow.min.js'
  export default {
    components: {
      Fix,Banner,Title,Coo
    },
    data(){
      return{
        pro2:[
          {
            pic:require("../assets/images/about/about_part02_pic01.png"),
            h4:"初创",
            p:"人人学苑最初成立于1999年，由浙江人人学苑教育科技有限公司组建的教育名牌，总部位于长三角南翼经济中心和浙江省经济中心——宁波。",
            class:"slideInLeft"
          },
          {
            pic:require("../assets/images/about/about_part02_pic02.png"),
            h4:"发展",
            p:"人人学苑先后与20多所高校及科研机构建立了联系。在宁波萌恒集团、迪蒙电动车、临海海商职业技术学校、三门职业教育集团等单位建立了实习实训和就业基地，构建了职业教育...",
            class:"slideInUp"
          },
          {
            pic:require("../assets/images/about/about_part02_pic03.png"),
            h4:"未来",
            p:"未来，人人学苑团队将继续努力在办学理念、人才培养模式、教学手段现代化等方面进一步求索创新、展望未来，学校将继续以特色办学、以质量取胜，继续遵循职业教育和成人高等教育发展规律...",
            class:"slideInRight"
          }
        ]
      }
    },
    methods:{
      menu() {
        window.scrollTo(0,0);
      }
    },
    created(){
      this.menu();
    },
    mounted(){
      new WOW().init();
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
  #about{
    width:100%;
  }
</style>



