<template>
  <div class="moreto">
    <p @click="more(msg)">more<span></span></p>
  </div>
</template>

<script>
  export default {
    props:["msg"],
    methods:{
      more(msg){
        this.$router.push({path:msg})
      },
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
  .moreto{
    position: absolute;
    top: 1rem;
    right: 1.2rem;
  }
  .moreto p {
    display: flex;
    align-items: center;
    font-size: 0.766rem;
    color: #7e7f80;
    cursor: pointer;
  }
  .moreto p span{
    display:block;
    width:0.7rem;
    height:0.7rem;
    background:url("../assets/images/home/right-icon2.png") no-repeat;
    background-size:100%;
    margin-left:0.3rem;
    margin-top:0.2rem;
  }
</style>



