<template>
  <div>
    <div class="fix" :class="{play:dian}">
      <div class="fix-left">
        <img src="../assets/images/code.png">
        <p>扫一扫关注人人微信</p>
      </div>
      <div class="fix-right">
        <p>关注<br>微信</p>
        <p class="ele"></p>
      </div>
    </div>
    <div class="bt" @click="turn"><img :class="{dao:dian}" src="../assets/images/aca/up.png"></div>
  </div>
</template>

<script>
export default {
  data(){
    return{
      dian:false,
    }
  },
  methods:{
    turn(){
      this.dian=!this.dian;
    }
  }
}
</script>

<!-- Add "scoped" attr     ibute to limit CSS to this component only -->
<style>
  .fix{
    position:fixed;
    right:4.5rem;
    top:50%;
    margin-top:-2.8rem;
    width:6.66rem;
    height:5.633rem;
    border:0.033rem solid #b3b3b3;
    overflow:hidden;
    background:#fff;
    z-index:999;
  }
  .play{
    display:none;
  }
  .fix .fix-left{
    float:left;
    width:4.933rem;
    height:5.633rem;
    border-right:0.033rem solid #b3b3b3;
  }
  .fix .fix-left img{
    width:4.933rem;
    height:4.933rem;
  }
  .fix .fix-left p{
    font-size:0.48rem;
    color: #b3b3b3;
    text-align:center;
  }
  .fix .fix-right{
    float:left;
  }
  .fix .fix-right p{
    box-sizing:border-box;
    font-size:0.48rem;
    color: #b3b3b3;
    padding:0.3rem;
  }
  .fix .fix-right .ele{
    width:1.68rem;
    height:2rem;
    padding:0.3rem 0.25rem;
    border-top:0.033rem solid #b3b3b3;
    border-bottom:0.033rem solid #b3b3b3;
    background:url("../assets/images/ele.png") no-repeat center center;
    background-size:75%;
    cursor:pointer;
  }
  .bt{
    position:fixed;
    width:1.7rem;
    height:1.715rem;
    right:4.5rem;
    top:50%;
    z-index:9999;
    margin-top:1.1rem;
    cursor: pointer;
    background: #fff;
    border:0.033rem solid #b3b3b3;
  }
  .dao{
    transform:rotate(180deg);
  }
  .bt img{
    width:1.2rem;
    height:1.2rem;
    margin:0.3rem auto;
    transition:all 0.5s;
  }
</style>



