<template>
  <div class="newque">
    <Banner msg="news and trends" msm="新闻动态"></Banner>
    <div class="newque-center">
      <h3>{{title}}</h3>
      <h5>
        发表时间：
        <span>{{time}}</span>
        分类：
        <span>{{type}}</span>
        来源：
        <span>{{from}}</span>
      </h5>
      <p v-for="item in list">{{item}}</p>
    </div>
  </div>
</template>

<script>
  import Banner from "../components/banner"
  export default {
    components: {
      Banner
    },
    data(){
      return{
        title:"祝贺人人学苑2016级成人教育毕业典礼圆满成功！",
        time:"2017-04-26",
        type:"学院动态",
        from:"人人学苑",
        list:[
          "昨日，2014级毕业典礼在我校人人学苑隆重召开！两年半的自我提升之路，2014级毕业学员们收获了终身受用的毕业证书。2014年，你在人人学苑报名；2016年，你从人人学苑毕业。一分耕耘，一分收获！恭喜各位毕业学员们！也许没有像大学那样的礼堂，华丽的布置典礼",
          "中国儿童中心和社会科学文献出版社18日在京联合发布的这份《中国儿童参与状况报告（2017）》，是在对全国7个城市（北京、无锡、安阳、贵阳、广州、哈尔滨、德阳）的8847名中小学生进行调查的基础上形成的研究成果。",
          "中小学生的课余时间，大量被用在了补习班和兴趣班上。报告显示，48.9%的中小学生都参加了和学校考试内容密切相关的补习班。其中，小学生参加补习班的比例为49.2%，初中生是51.7%，高中生是45.9%。有25.3%的中小学生参加了两门及以上的补习班。同时，接近两成的中小学生参加了两门及以上的兴趣班。",
          "报告还显示，在家庭参与方面，儿童与家长的沟通话题单一。90%以上的父母都会和儿童谈论学习问题。但是，有31.5%的父母从来不和孩子谈论怎么交朋友，26.3%的父母从来不和孩子谈论怎么用钱。",
          "研究人员建议，学校、家庭和社会应当共同推动儿童参与。尤其是学校可以开设更多与生活、与儿童兴趣发展相关的主题课程，增加学生综合实践的机会；家长要了解与尊重儿童，协助儿童安排好做作业、看电视、做家务、体育锻炼、游戏娱乐、课外交往等内容。",
          "据香港东方日报网18日报道，理工大学做出不再续约的决定后，一些理大学生及团体表示不满，质疑理大的决定欠缺说服力。“大学及专上院校工会联盟”(简称“大专联”)称，郑松泰倒插国旗是为表达政见，院校应尊重其自由;理大不续约的手法过于严苛，恐引发寒蝉效应。18日，理大在校园民主墙反驳称，教职员纪律委员会认为郑松泰侮辱五星红旗及香港区旗一事，为大学教师及学生树立了不良榜样，有损公众人士对理大师资的信心。校方还说，理工大学一直致力于推动优质教育、促进国际化，鼓励尊重与包容，郑松泰侮辱国旗及区旗的行为明显与校方的目标背道而驰。校方随即执行相关裁决及建议，包括不与他续约，以及安排另一个本学期执教同一科目的老师代替郑松泰授课。",
          "郑松泰多次煽动激进分子组织反水货客行动，在香港社会引发争议。2016年10月19日香港立法会第二次会议上，他将民建联议员插在台上的中国国旗和香港区旗倒转，随即被立法会主席裁定行为不检而赶出会场。同年12月，有议员提出动议，认为郑松泰行为不检，要求根据《基本法》取消其议员资格，立法会成立调查委员会进行调查。去年4月，香港警方正式落案起诉郑松泰“侮辱国旗罪”及“侮辱区旗罪”，9月裁定两项控罪成立，每项各判罚款2500港元。"
        ]
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
  .newque{
    width:100%;
  }
  .newque .banner-com{
    background:url("../assets/images/news/news_banner.png") no-repeat;
    background-size:100%;
  }
  .newque .newque-center{
    width:48rem;
    margin:2rem auto;
  }
  .newque .newque-center h3{
    font-size:1.2rem;
    color:#333232;
    text-align:center;
    letter-spacing:0.1rem;
  }
  .newque .newque-center h5{
    font-size: 0.65rem;
    color: #999696;
    text-align: center;
    font-weight: 500;
    margin: 1rem 0;
  }
  .newque .newque-center h5 span{
    margin-right: 0.8rem;
  }
  .newque .newque-center p{
    font-size: 0.7rem;
    color:#323232;
    text-align: left;
    line-height: 1.2rem;
    text-indent: 2em;
    margin-top:0.65rem;
  }
</style>



