<template>
  <div id="onjob" class="online">
    <Banner msg="Online education" msm="在线教育"></Banner>
    <Nav :msg="nav" :msm="nav2"></Nav>
    <div class="onjob-con">
      <div class="onjob-center">
        <ul class="left-list animated slideInLeft">
          <li v-for="( item , i ) in listL">
            <h2  @click="hDian(item)">{{item.title}}<span v-if="item.spanAC1">+</span><span v-if="item.spanAC2">-</span></h2>
            <p v-for="( its , m ) in item.fen" :class="{fontY:isRight(i,m)}" v-if="item.pActive" @click="pActive(i,m,item)">{{its}}</p>
          </li>
        </ul>
        <ul class="right-list  animated slideInRight">
          <li v-for="( list , n) in listR">
            <div v-for="( item , c) in list.con" :class="{display:isRight(n,c)}">
              <h3>{{item.title}}</h3>
              <p v-for="its in item.con1">{{its}}</p>
              <h3>{{item.hcc}}</h3>
              <p v-for="itm in item.con2">{{itm}}</p>
              <h3>{{item.hmc}}</h3>
              <p v-for="itp in item.con3">{{itp}}</p>
            </div>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
  import Banner from "../../components/banner"
  import Nav from "../../components/nav"
  export default {
    components: {
      Banner, Nav,
    },
    data() {
      return {
        nav: "首页 > 在线教育 >在职研究生 >",
        nav2: "武汉科技大学>招生信息",
        nowindex:[0,0],
        listL:[
          {
            title:"武汉科技大学",
            spanAC1:false,
            spanAC2:true,
            fen:["招生信息","报考信息","毕业报销","考前辅导"],
            pActive:true,
          },
          {
            title:"浙江财经大学",
            spanAC1:true,
            spanAC2:false,
            fen:["招生信息","报考专业",],
            pActive:false,
          }
        ],
        listR:[
          {
            con:[
              {
                title:"报读院校：武汉科技大学",
                con1:[
                  "武汉科技大学（代码：10488））是以工科为主、理工结合，工、理、管、医、文、经、 法、哲、艺等学科协调发展的综合性大学。学校积极实施“人才强校”战略，形成了一 支以“千人计划”、“长江学者”、“百人计划”、“楚天学者”计划入选者为核心的 高层次人才队伍， 拥有双聘院士8人，国家“千人计划”创新人才6人、“长江学者”1 人、湖北省“百人计划”9人。学校坚持深化教育教学改革，先后获批国家教育质量工程 项目49项，数量位居省属高校第一；获得国家教学成果奖4项，数量位居全国高校前15位。"
                ],
                hcc:"报读专业：工商管理硕士（MBA）",
                con2:[
                  "学制3年，弹性学习，节假日集中授课",
                  "宁波当地开班",
                  "学费：5.4万元（分三年交）"
                ]
              },
              {
                title:"报考条件：",
                con1:[
                  "1. 大学本科毕业后有3年以上工作经验的人员（2015年9月前毕业）；",
                  "2. 获得国家承认的高职高专毕业学历后，有5年以上工作经验（2013年9月前毕业）；",
                  "3. 达到与大学本科毕业生同等学力的人员。"
                ],
                hcc:"报考流程：",
                con2:[
                  "1. 咨询登记：2017年7月~12月（0574-27826669、13867450832） 2. 网上报名：2017年10月（中国研究生招生信息网http://yz.chsi.cn）",
                  "2. 网上报名：2017年10月（中国研究生招生信息网http://yz.chsi.cn）",
                  "3. 现场确认：2017年11月（具体以学校研究生院公告为准） ",
                  "4. 初试：时间以教育部公布时间为准（12月底）；地点由各报考点安排（宁波）； ",
                  "5. 复试：复试时间、地点由学校统一安排（宁波）； ",
                  "6. 录取：对初试复试成绩合格、学历认证合格、思想政治考核合格及体检合格的考生予以录取。"
                ]
              },
              {
                title:"学历与学位",
                con1:[
                  "1. 颁发武汉科技大学硕士研究生毕业证书",
                  "2. 授予武汉科技大学硕士学位"
                ],
                hcc:"补贴标准",
                con2:[
                  "企业人员攻读研究生毕业后可享受学费50%的补贴，每人补贴额度最高不超过5万元。",
                  "详见：宁波市人力资源和社会保障局办公室文件《关于做好2017年企业在职人员攻读研究 生学费补贴有关工作的通知》（甬人社办发[2017]40号）。",
                ]
              },
              {
                title:"师资情况：",
                con1:[
                  "1. 太奇师资，专注在职考研培训19年",
                  "2. 历年考试通过率84.6%"
                ],
                hcc:"课程介绍：",
                con2:[
                  "1. 面授+网课；",
                  "2. 系统+串讲+押题=312课时；",
                  "3. 9月开班，周末宁波鼓楼上课；",
                  "4. 辅导费用：14800元，未过协议退款10000元或第二年免费。"
                ],
                hmc:"考试范围：",
                con3:[
                  "1.（课程代码：199）管理类综合能力测试：含数学、逻辑、写作",
                  "2.（课程代码：204）英语二"
                ]
              },
            ]
          },
          {
            con:[
              {
                title:"报读院校：浙江财经大学",
                con1:[
                  "浙江财经大学是一所经济学科和管理学科特色明显，办学优势突出的多科性教学研究型省属财经大学，是浙江省财经管理骨干人才的重要培育基地。学校坐落在浙江省会城市杭州市，前身是创建于1974年的浙江财政银行学校，1987年经原国家教委批准成立浙江财经学院，原隶属于浙江省财政厅，后由浙江省人民政府主管。2006年获教育部本科教学工作水平评估优秀。2013年经教育部批准更名为浙江财经大学。",
                ],
                hcc:"师资力量",
                con2:[
                  "截至2017年11月，浙江财经大学共有在校教职工1492人，其中专任教师1080人，正高级职称162人，副高级职称345人，具有博士学位专任教师542人。拥有国家“万人计划”哲学社会科学领军人才1人，国家级有突出贡献中青年专家1人，国家宣传文化系统“四个一批”人才1人，“百千万人才工程”国家级人选2人，教育部“高校青年教师奖”获得者1人，教育部“新世纪优秀人才支持计划”2人，享受国务院政府特殊津贴12人，浙江省特级专家1人，省部级有突出贡献中青年专家3人，浙江省“新世纪151人才工程”第一、二层次入选者22人（含重点资助3人），浙江省高校中青年学科带头人31人，浙江省宣传文化系统“五个一批”人才4人，全国模范教师1人，全国优秀教师2人，全国师德先进个人1人，浙江省高校优秀教师10人，浙江省高校教学名师5人，浙江省教坛新秀7人，浙江省师德先进个人7人。另聘有100余名知名专家、学者为兼职教授。另外，该校还拥有1个国家级教学团队、4个省级教学团队"
                ],
              },
              {
                title:"报考专业",
                con1:[
                  "学校设有应用经济学、理论经济学、工商管理、公共管理、法学、统计学等6个一级学科硕士点，工商管理、会计、金融、保险、税务、资产评估、公共管理和国际商务等8个专业硕士学位授予点，招生硕士专业42个，硕士点布局涵盖了经、管、文、法、理、哲等六大学科门类。经济管理类专业的硕士点覆盖面居浙江省属高校前茅，目前全校在校生总数1700余人。",

                ],
                hcc:"MBA项目",
                con2:["\"浙江财经学院工商管理硕士（MBA）项目的培养目标是在坚持通用办学模式基础上，积极探索个性化、专业化发展的道路，突出体现财经特色，培养具有良好职业道德和社会责任感、富于创新和创业精神、具备战略意识和国际化视野、财经知识丰富的中高级应用型、专业型管理人才。培养方向：财务规划与控制、金融投资管理、人力资源管理、企业运作管理。"]
              }
            ],
          }
        ]
      }
    },
    methods:{
      pActive(a,b,con){
        this.nowindex = [a,b];
        this.nav2=con.title+">"+con.fen[b];
      },
      hDian(item){
        item.spanAC1=!item.spanAC1;
        item.spanAC2=!item.spanAC2;
        item.pActive=!item.pActive;
      },
      isRight(a,b){
        if(this.nowindex[0]===a&&this.nowindex[1]===b){
          return true;
        }else {
          return false;
        }
      },
      menu() {
        window.scrollTo(0,0);
      }
    },
    created(){
      this.menu();
    }
  }
</script>

