<template>
  <div id="adult" class="online">
    <Banner msg="Online education" msm="在线教育"></Banner>
    <Nav :msg="nav" :msm="nav2"></Nav>
    <div class="adult-con">
      <div class="adult-center">
        <ul class="left-list animated slideInLeft">
          <li v-for="( item , i ) in listL">
            <h2  @click="hDian(item)">{{item.title}}<span v-if="item.spanAC1">+</span><span v-if="item.spanAC2">-</span></h2>
            <p v-for="( its , m ) in item.fen" :class="{fontY:isRight(i,m)}" v-if="item.pActive" @click="pActive(i,m,its)">{{its}}</p>
          </li>
        </ul>
        <ul class="right-list  animated slideInRight">
          <li v-for="( list , n) in listR">
            <div v-for="( item , c) in list.con" :class="{display:isRight(n,c)}">
              <h3>{{item.title}}</h3>
              <p v-for="its in item.con">{{its}}</p>
              <p>
                <strong>特色专业：</strong>
                {{item.end}}
              </p>
              <div class="pic">
                <img :src="item.pic1">
                <img :src="item.pic2">
              </div>
            </div>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
  import Banner from "../../components/banner"
  import Nav from "../../components/nav"
  export default {
    components: {
      Banner, Nav,
    },
    data() {
      return {
        nav: "首页 > 在线教育 >成人高考 >",
        nav2: "浙江大学宁波理工学院",
        nowindex:[0,0],
        listL:[
          {
            title:"学校名称",
            spanAC1:false,
            spanAC2:true,
            fen:["浙江大学宁波理工学院","丽水学院","宁波职业技术学院","宁波城市职业技术学院"],
            pActive:true,
          }
        ],
        listR:[
          {
            con:[
              {
                title:"浙江大学宁波理工学院",
                con:[
                  "浙江大学宁波理工学院（Ningbo Institute of Technology,Zhejiang University）成立于2001年 6月，是一所经中华人民共和国教育部和浙江省人民政府批准，由宁波市人民政府全额投资建设，浙江 大学负责办学管理的全日制普通本科院校，是宁波市属事业单位。",
                  "浙江大学宁波理工学院占地约1200亩，建筑面积约36万平方米；下属9各学院（系）,开设37个本科专业以及 3个中外合作专业；共有全日制本科生1.1万余人，职教工1000余名。"
                ],
                end:"财务管理、工程管理、旅游管理、市场营销、国际经济与贸易，机械制造及其 自动化、土木工程、电气工程及其自动化。",
                pic1:require("../../assets/images/online/adult1-1.jpg"),
                pic2:require("../../assets/images/online/adult1-2.jpg"),
              },
              {
                title:"丽水学院",
                con:[
                  "丽水学院（Lishui University）位于浙江省丽水市，由丽水市人民政府举办，其办学历史可追溯到1907的处州 师范学堂。2004年5月经教育部批准在丽水师范专科学校和丽水职业技术学院合并的基础上升格更名为丽水学院， 是浙江省公办全日制普通本科高校。",
                  "校园占地面积1008.16亩，校舍建筑面积38.74万平方米。下设7个二级学院，有45个本科专业，涵盖了经济学、 法学、教育学、文学、理学、工学、农学、管理学、医学等学科门类，24个高职专科专业；全日制在校生12607人 ，其中普通本科生10681人。"
                ],
                end:"专科：学前教育、临床医学、护理       本科：学前教育、小学教育、口腔医学、护理学、电子商务、园林",
                pic1:require("../../assets/images/online/adult2-1.jpg"),
                pic2:require("../../assets/images/online/adult2-2.jpg"),
              },
              {
                title:"宁波职业技术学院",
                con:[
                  "宁波职业技术学院（Ningbo Polytechnic）简称“宁职院”,是一所由中华人民共和国教育部批准设立的 从事高等职业教育的普通全日制高等学校，2006年入选首批国家示范性高等职业院校。",
                  "宁波职业技术学院于1999年由宁波中等专业学校和宁波市职工业余大学合并而来；宁波中等专业学校创建于 1984年。现有全日制高职在校生9000余名，非全日制学生和各类培训人员16000人。"
                ],
                end:"工商企业管理、人力资源管理。行政管理、会计、建筑工程技术、机电 一体化技术、模具设计与制造、应用化工技术、电气自动化技术。",
                pic1:require("../../assets/images/online/adult3-1.jpg"),
                pic2:require("../../assets/images/online/adult3-2.jpg"),
              },
              {
                title:"宁波城市职业技术学院",
                con:[
                  "宁波城市职业技术学院于2003年3月经浙江省人民政府批准设置而成。学校的前身——宁波大学职业技术 教育学院，创建于1992年，是浙江省最早开展全日制高等职业技术教育的机构，曾被原浙江省教委确定为省职业技术教育 师资培训基地。2001年，“旅游管理与服务”专业被教育部确定为高职高专教育教学改革试点专业。",
                ],
                end:"工商企业管理、国际经济与贸易、会计、旅游管理、建筑装饰 工程技术、园林技术、机电一体化、计算机应用技术。",
                pic1:require("../../assets/images/online/adult4-1.jpg"),
              }
            ]
          }
        ]
      }
    },
    methods:{
      pActive(a,b,con){
        this.nowindex = [a,b];
        this.nav2=con;
      },
      hDian(item){
        item.spanAC1=!item.spanAC1;
        item.spanAC2=!item.spanAC2;
        item.pActive=!item.pActive;
      },
      isRight(a,b){
        if(this.nowindex[0]===a&&this.nowindex[1]===b){
          return true;
        }else {
          return false;
        }
      },
      menu() {
        window.scrollTo(0,0);
      }
    },
    created(){
      this.menu();
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style></style>



